// Display extensions
@include responsive-display-classes((xs: inherit, smm: inherit, sm: inherit, md: inherit, lg: inherit, dlg: inherit, xl: inherit, extra: inherit, xxl: inherit, 3xl: inherit, 4xl: inherit), display);
@include responsive-display-classes((xs: initial, smm: initial, sm: initial, md: initial, lg: initial, dlg: initial, xl: initial, extra: initial, xxl: initial, 3xl: initial, 4xl: initial), display);

// Colors
$theme-main-navDdropdown-bg-color: #{$body-bg};
$theme-main-nav-link-color: #{$body-color};

:root {
    --theme-main-container-max-width: calc(100% - (#{$offsetWidth} * 2));
    --theme-main-gutter: #{$offsetWidth} - #{$grid-gutter-width};
    --theme-main-dropdown-top: 105%;
    --theme-main-navDdropdown-bg: $theme-main-navDdropdown-bg-color;
    --theme-main-nav-link-color: $theme-main-nav-link-color;
    --theme-main-nav-item-delay: 0.0s;
    --theme-main-start-opacity: 0;
    --theme-main-end-opacity: 1;
    --theme-main-footer-background-color: #{$dark};
    --theme-main-footer-color: #{$light};
    --theme-main-footer-background-gradient-top: #{$primary};
    --theme-main-footer-background-gradient-bottom: #{$dark};
    --theme-main-footer-background-gradient: radial-gradient(at bottom right, var(--theme-main-footer-background-gradient-bottom) 10%, var(--theme-main-footer-background-gradient-top) 76%);

    @include media-breakpoint-up(md) {
        --theme-main-container-max-width: calc(100% - (#{$offsetWidthMD} * 2));
        --theme-main-gutter: #{$offsetWidthMD} - #{$grid-gutter-width};
    }

    @include media-breakpoint-up(lg) {
        --theme-main-container-max-width: calc(100% - (#{$offsetWidthLG} * 2));
        --theme-main-gutter: #{$offsetWidthLG} - #{$grid-gutter-width};
    }

    @include media-breakpoint-up(dlg) {
        --theme-main-container-max-width: calc(100% - (#{$offsetWidthDLG} * 2));
        --theme-main-gutter: #{$offsetWidthDLG} - #{$grid-gutter-width};
    }

    @include media-breakpoint-up(xl) {
        --theme-main-container-max-width: calc(100% - (#{$offsetWidthXL} * 2));
        --theme-main-gutter: #{$offsetWidthXL} - #{$grid-gutter-width};
    }

    @include media-breakpoint-up(xxl) {
        --theme-main-container-max-width: calc(100% - (#{$offsetWidthXXL} * 2));
        --theme-main-gutter: #{$offsetWidthXXL} - #{$grid-gutter-width};
    }

    @include media-breakpoint-up(3xl) {
        --theme-main-container-max-width: calc(100% - (#{$offsetWidth3XL} * 2));
        --theme-main-gutter: #{$offsetWidth3XL} - #{$grid-gutter-width};
    }

    @include media-breakpoint-up(4xl) {
        --theme-main-container-max-width: calc(100% - (#{$offsetWidth4xl} * 2));
        --theme-main-gutter: #{$offsetWidth4xl} - #{$grid-gutter-width};
    }

    --theme-main-gutter-x: calc(var(--theme-main-gutter) + (#{$grid-gutter-width} * 2));
}

// Other existing styles...
.full-width {
    @include media-breakpoint-down(md) {
        margin-left: calc(0px - var(--theme-main-gutter) - 4px);
        margin-right: calc(0px - var(--theme-main-gutter) - 4px);
    }

    margin-left: calc(0px - var(--theme-main-gutter));
    margin-right: calc(0px - var(--theme-main-gutter));
    padding-right: calc(var(--bs-gutter-x) / 2);
    padding-left: calc(var(--bs-gutter-x) / 2);

}

