.wp-admin .editor-styles-wrapper {
    .full-width {
        @include media-breakpoint-down(md) {
            margin-left: auto !important;
            margin-right: auto !important;
        }

        margin-left: auto !important;
        margin-right: auto !important;
    }

    .full-height {
        min-height: 450px;
    }

    .position-sticky {
        z-index: 1 !important;
    }
}

@mixin container-max-width($max-width) {
    max-width: $max-width;
}

.container {
    max-width: var(--theme-main-container-max-width);
}
.wp-block-group__inner-container {
    max-width: var(--theme-main-container-max-width) !important;
}
.wp-block-group__inner-container {
    @extend .container;
}

@include media-breakpoint-up(lg) {
    .container-lg {
        max-width: var(--theme-main-container-max-width);
    }
}

@include media-breakpoint-up(md) {
    .container-md {
        max-width: var(--theme-main-container-max-width);
    }
}

@include media-breakpoint-up(sm) {
    .container-sm {
        max-width: var(--theme-main-container-max-width);
    }
}

@include media-breakpoint-up(xl) {
    .container-xl {
        max-width: var(--theme-main-container-max-width);
    }
}

@include media-breakpoint-up(xxl) {
    .container-xxl {
        max-width: var(--theme-main-container-max-width);
    }
}

@include media-breakpoint-up(dlg) {
    .container-dlg {
        max-width: var(--theme-main-container-max-width);
    }
}

@include media-breakpoint-up(3xl) {
    .container-3xl {
        max-width: var(--theme-main-container-max-width);
    }
}

@include media-breakpoint-up(4xl) {
    .container-4xl {
        max-width: var(--theme-main-container-max-width);
    }
}

// Mixin for Gutter Spacing
@mixin margin-spacing($property, $value) {
    margin-#{$property}: $value;
}

@mixin padding-spacing($property, $value) {
    padding-#{$property}: $value;
}

// Apply Gutter Spacing Mixin
.mt-gutter {
    @include margin-spacing(top, var(--theme-main-gutter-x));
}

.mb-gutter {
    @include margin-spacing(bottom, var(--theme-main-gutter-x));
}

.my-gutter {
    @include margin-spacing(top, var(--theme-main-gutter-x));
    @include margin-spacing(bottom, var(--theme-main-gutter-x));
}

.m-gutter {
    @include margin-spacing(all, var(--theme-main-gutter-x));
}

.ms-gutter {
    @include margin-spacing(left, var(--theme-main-gutter-x));
}

.me-gutter {
    @include margin-spacing(right, var(--theme-main-gutter-x));
}

.mx-gutter {
    @include margin-spacing(left, var(--theme-main-gutter-x));
    @include margin-spacing(right, var(--theme-main-gutter-x));
}

.pt-gutter {
    @include padding-spacing(top, var(--theme-main-gutter-x));
}

.pb-gutter {
    @include padding-spacing(bottom, var(--theme-main-gutter-x));
}

.py-gutter {
    @include padding-spacing(top, var(--theme-main-gutter-x));
    @include padding-spacing(bottom, var(--theme-main-gutter-x));
}

.p-gutter {
    @include padding-spacing(all, var(--theme-main-gutter-x));
}

.ps-gutter {
    @include padding-spacing(left, var(--theme-main-gutter-x));
}

.pe-gutter {
    @include padding-spacing(right, var(--theme-main-gutter-x));
}

.px-gutter {
    @include padding-spacing(left, var(--theme-main-gutter-x));
    @include padding-spacing(right, var(--theme-main-gutter-x));
}